import React from 'react';
import './Item.css';


function Item(props){
    return (
        <div className="contacts-item" >
            <div class="contacts-first-letter">
                <span>{props.item.name.charAt(0).toUpperCase()}</span>
            </div>

            <div class="contacts-item-content">
                <span id="name" class="data">{props.item.name.toLowerCase().split(' ').map(word =>
                        word.charAt(0).toUpperCase() + word.slice(1)).join(' ')}</span>
                <span id="number" class="data">{props.item.number}</span>
            </div>
            <span class="delete-btn" title="delete" onClick={(e) => {
                //e.stopPropagation();
                props.onDeleteItem(props.item);
            }}></span>
        </div>
    );
}

export default Item;