import React from 'react';
import './Footer.css';

class Footer extends React.Component {

  constructor(props){
    super(props);

    this.state = {
      name: "",
      number: ""
    }

    this.handleChangeName = this.handleChangeName.bind(this);
    this.handleChangeNumber = this.handleChangeNumber.bind(this);
  }

  handleChangeName(event){
    this.setState({ name: event.target.value });
  }

  handleChangeNumber(event){
    this.setState({ number: event.target.value });
  }

  enterData(event){
    let myAddButton = document.getElementById("add-btn");
    let nameField = document.getElementById("name-field");
    if(event.keyCode === 13){      
      myAddButton.click();
      nameField.focus();
    }
  }

  setFocusNumber(event){
    let numberField = document.getElementById("number-field");
    if(event.keyCode === 13){      
      numberField.focus();
    }
  }

  render(){
    return (
      <footer>
        <div class="content">
          <div class ={`input-icons ${this.props.warningName === 1 ? 'warning' : ''}`}> 
            <i class="fa fa-user icon"></i> 
            <input id="name-field" onKeyUp={this.setFocusNumber} class="input-field" value={this.state.name} onChange={this.handleChangeName} placeholder="my contact's name..." /> 
          </div> 
          <div class={`input-icons ${this.props.warningPhone === 1 ? 'warning' : ''}`}> 
            <i class="fas fa-phone-alt icon"></i>
            <input id="number-field" onKeyUp={this.enterData} class="input-field" value={this.state.number} onChange={this.handleChangeNumber} placeholder="my contact's phone number..." /> 
          </div>
        </div>
        <span id="add-btn" onClick={() => {
          this.props.createItem(this.state.name, this.state.number); 
          if(this.state.name !== '' && this.state.number !== ''){
            this.setState({name: ''});
            this.setState({number: ''});
          } 
          }}>Add</span>
      </footer>
    )
  }
}

export default Footer;
