import React from 'react';
import './Body.css';
import ItemsAll from '../items-all/ItemsAll';



class Body extends React.Component {

  constructor(props){
    super(props);

    this.state = {
      searchQuery: ""
    }

    this.handleChangeQuery = this.handleChangeQuery.bind(this);
    this.enterKey= this.enterKey.bind(this);
  }

  handleChangeQuery(event){
    this.setState({ searchQuery: event.target.value });
  }

  enterKey(event){
    let myButton = document.getElementById("search-field-btn");
    if(event.keyCode === 13){
      myButton.click();
    }
  }


  render(){
    return (
      <div id="app-body" >
        <div id="header">
            <span>MY CONTACTS</span>
            <i class="fas fa-search" onClick={this.props.searchSymbol} id="search-btn"></i>
        </div>
        <div className={this.props.searchIndex === 1 ? 'search' : 'hide'}>
            <input ref="searchfield" onKeyUp={this.enterKey} type="search" id="search-field" value={this.state.searchQuery} onChange={this.handleChangeQuery} placeholder="search by name" autoFocus />
            <button id="search-field-btn" onClick={() => {this.props.searchWithField(this.state.searchQuery);
                                                          this.setState({searchQuery: ""});
            }}>Search</button>
        </div>
        <ItemsAll items={this.props.items} onDeleteItem={this.props.onDeleteItem}></ItemsAll>
      </div>
    )
  }
}

export default Body;
