import React from 'react';
import './App.css';
import Body from './components/body/Body';
import Footer from './components/footer/Footer';
import ContactItem from './model/ContactItem';
import Contacts from './model/Contacts';

class App extends React.Component{

  constructor(props){
    super(props);

    this.contacts = new Contacts();

    this.state = {
      items: this.contacts.items,
      warningUser: -1,
      warningPhone: -1,
      searchIndex : -1
    }

    this.createItem = this.createItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.searchSymbol = this.searchSymbol.bind(this);
    this.searchWithField = this.searchWithField.bind(this);

  }

  createItem(name, number){

    if(name !== '' && number !== ''){
      let item = new ContactItem(name, number);
      let items = this.contacts.add(item);

      this.setState({items: items});
      this.setState({warningUser: -1});
      this.setState({warningPhone: -1});
    } else if(name !== '' && number === ''){
      this.setState({warningPhone: 1});
    } else if (name === '' && number !== ''){
      this.setState({warningUser: 1});
    } else {
      this.setState({warningUser: 1});
      this.setState({warningPhone: 1});
    }

  }

  deleteItem(item){
    let items = this.contacts.delete(item);

    this.setState({items: items});
  }

  searchSymbol(){
    this.setState({searchIndex: 1});
  }

  searchWithField(searchQuery){      
    
    let result = this.contacts.items.find(function(item){
      return item.name.toLowerCase().includes(searchQuery.toLowerCase());
    });

    if(!result || searchQuery === '') {         
      alert("You do not have this contact!");
    } else {
      alert(result.name + '\n' + result.number);
    }

    this.setState({searchIndex: -1});
  }


  render (){
    return (
      <div id="app-container">
        <Body items={this.state.items} onDeleteItem={this.deleteItem} searchWithField={this.searchWithField} searchSymbol={this.searchSymbol} searchIndex={this.state.searchIndex} />
        <Footer createItem = {this.createItem} warningName={this.state.warningUser} warningPhone={this.state.warningPhone} />
      </div>
    )
  }
}

export default App;
